@import "mixins/_mixins.scss";

// base
@import "base/_variables.scss";
@import "base/_normalize.scss";
@import "base/_type.scss";
@import "base/_reboot.scss";

// Partials
@import "partials/_homepage.scss";
@import "partials/_slider.scss";
@import "partials/_fontello.scss";

// vendor
@import "vendor/_jquery.fullpage.scss";

$enable-flex: true;
$enable-grid-classes: true;

html {
    overflow-x: hidden;
}

body {
    color: $text-color;
    overflow-x: hidden;
}

h1 {
    margin-top: 50px;
}

a {
    transition: all 0.4s;

    i {
        transition: all 0.4s;
    }
}

.fp-viewing-home {
    .nav {
        transition: all 0.4s;
        background: rgba(255, 255, 255, 1);
    }
}

.fp-viewing-about {
    .nav {
        transition: all 0.4s;
        background: rgba(255, 255, 255, 0.95);
    }
}

.fp-viewing-work {
    .nav {
        transition: all 0.4s;
        background: rgba(255, 255, 255, 0.95);

        @media only screen and (min-width: 768px) {
            background: rgba(255, 255, 255, 0);
        }
    }
}

.fp-viewing-contact {
    .nav {
        transition: all 0.4s;
        background: rgba(255, 255, 255, 1);
    }
}

.nav {
    position: fixed;
    padding: 20px;
    right: 0;
    margin: auto;
    width: 100%;
    z-index: 99999;
    transition: all 0.6s;
    height: 120px;

    ul {
        margin: 0;

        li {
            display: inline-block;
            position: relative;
            padding: 0 5px;
            float: left;
            line-height: 54px;

            @media only screen and (min-width: 768px) {
                padding: 0 10px;
            }

            &.logo {
                font-family: $text-chitown;
                line-height: 54px;

                h2 {
                    font-size: 60px;
                }

                a {
                    height: 81px;
                    top: 0;

                    &:hover {
                        &:before {
                            display: none;
                        }
                    }
                }
            }

            a {
                padding: 12px 5px;
                display: block;
                color: black;
                position: relative;
                text-align: center;
                float: left;
                top: 2px;

                @media only screen and (min-width: 768px) {
                    padding: 12px 10px;
                }

                &:before {
                    display: block;
                    content: "";
                    position: absolute;
                    bottom: 10px;
                    height: 2px;
                    width: 0;
                    transition: 0.3s width;
                    left: 0;
                }

                &:hover {
                    &:before {
                        display: block;
                        content: "";
                        position: absolute;
                        background: $text-color;
                        width: 100%;
                    }
                }
            }
        }
    }
}

.about-me .fp-tableCell {
    .intro {
        max-width: 900px;
        margin: 0 auto;

        .about-image {
            display: inline-block;
            border-radius: 100px;
            overflow: hidden;
            float: left;
            margin-bottom: 15px;

            img {
                height: 100px;
                width: 100px;
                float: left;
            }
        }

        .about-title {
            display: inline-block;
            float: left;
            margin-bottom: 15px;

            h1 {
                line-height: 50px;
                margin: 0;
                text-align: left;
            }

            @media only screen and (min-width: 768px) {
                margin-left: 25px;

                h1 {
                    line-height: 100px;
                }
            }
        }

        p {
            text-align: justify;
            clear: both;
            font-size: 14px;
        }
    }

    .btn {
        float: left;
    }
}

.btn {
    display: inline-block;
    font-weight: 400;
    line-height: 1.25;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    border-radius: 0.25rem;
    transition: all 0.2s ease-in-out;

    &.btn-primary {
        color: $text-color;
        background-color: transparent;
        border-color: $text-color;

        &:hover {
            color: #292b2c;
            background-color: #fff;
            border-color: #adadad;

            &.icon {
                left: 10px;
                position: relative;
            }
        }
    }

    &.btn-secondary {
        color: #292b2c;
        background-color: #fff;
        border-color: #ccc;

        &:hover {
            color: #292b2c;
            background-color: #e6e6e6;
            border-color: #adadad;
        }
    }
}

.fp-tableCell {
    padding: 0 40px;
}

.logo-list {
    width: 100%;
    max-width: 900px;
    opacity: 0.8;
    // position: absolute;
    top: 0;
    // display: flex;
    // flex-wrap: wrap;
    // flex-direction: row;
    height: 100%;
    // justify-content: center;
    // align-content: space-evenly;
    // float: left;
    right: 0;
    left: 0;
    margin: auto;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    li {
        max-width: 125px;
        display: flex;
        min-height: 100px;
        min-width: 120px;
        float: left;

        a {
            display: flex;
            padding: 10px;
            align-self: center;

            img {
                width: 100%;
                float: left;
                align-self: center;
            }
        }
    }

    @media only screen and (min-width: 768px) {
        li {
            width: 15%;
            max-width: 158px;
            display: flex;
            min-height: 150px;
            min-width: 170px;

            a {
                padding: 20px;
            }
        }
    }
}