//
// Slider
// --------------------------------------------------

.hero {
    position: relative;

    .slide-item {
        img {
            // max-width: 80%;
            // max-height: 400px;
            width: auto;
            // height: auto;


            @media only screen and (min-width: 768px) {
                min-width: 100%;
                max-width: 130%;
            }

            @media only screen and (max-width: 768px) {
                max-height: 100%;
                min-width: auto;
            }

            object-fit: fill;
            object-position: center;
            //   }
        }
    }
}

.slick-slider-wrapper {
    width: 100%;
}

.slick-slider {
    width: 100%;
    height: 100%;
    max-width: 100%;
    overflow-x: hidden;
}

.slide-item {
    width: 100%;
    height: 100vh;
    display: inline-block;
    position: relative;
    padding-top: 15vh;
    overflow: hidden;

    .slide-overlay {
        z-index: 3;
        height: 100%;
        width: 100%;
        background: rgba(255, 255, 255, 0.2);
        position: absolute;
        top: 0;
    }

    .bg-hero {
        position: absolute;
        top: 120px;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 2;
        width: 100%;
        // margin-top: 120px;

        img {
            transform: translate3d(-60px, -5px, 0px);
            opacity: 0;
        }
    }

    // overflow: hidden;
    .slide-desc {
        position: absolute;
        left: 40px;
        bottom: 40px;
        text-align: left;
        opacity: 0;
        z-index: 4;

        h3 {
            font-weight: 200;
            margin: 20px 0;
        }

        h3:first-of-type {
            color: white;
            font-weight: 600;
        }
    }

    &.slick-active {
        .slide-desc {
            transform: translate3d(0, 0px, 0px);
            transition: all 2.6s ease-out;
            transition-delay: 0.2s;
            opacity: 1;
        }

        img {
            transform: translate3d(0, 0px, 0px);
            transition: all 5s ease-out;
            transition-delay: 0.5s;
            opacity: 1;
        }
    }

    &.slide-number-premier.slick-active {
        img {
            transform: translate3d(-70px, 0px, 0px);
        }
    }

    @media (min-width: $screen-sm-min) {
        .bg-hero {
            margin-top: 0;

            img {
                transform: translate3d(-30px, -5px, 0px);
            }
        }

        &.slide-number-premier.slick-active {
            img {
                transform: translate3d(-100px, 0px, 0px);
            }
        }
    }
}

#slick-1 .slick-dots {
    width: 50px;
    position: absolute;
    right: 0;
    bottom: 40px;
    display: none;
}

#slick-1 .slick-dots li {
    width: 40px;
    height: 5px;
    background: #ccc;
}

#slick-1 .slick-dots li button {
    width: 40px;
    height: 5px;
}

#slick-1 .slick-dots li.slick-active,
#slick-1 .slick-dots li:hover {
    background: #777;
}

#slick-1 .slick-dots li button,
#slick-1 .slick-dots li button:before {
    color: transparent;
    opacity: 0;
}

/* progress bar */
.slider-progress {
    width: 85px;
    // height: 3px;
    position: absolute;
    right: 40px;
    bottom: 30px;

    .progress-bar {
        background: #fff;

        .progress {
            width: 0%;
            height: 3px;
            background: #000;
        }
    }

    .slider-nav {
        margin: 20px 0;

        a {
            width: 40px;
            height: 40px;
            border-radius: 35px;
            background: white;
            display: inline-block;

            i {
                display: inline-block;
                vertical-align: middle;
                line-height: 40px;
                color: $text-color;
                position: relative;
            }

            &:hover {
                background: $text-color;

                i {
                    color: white;
                }
            }

            &.prev {
                i {
                    right: 2px;
                }
            }

            &.next {
                i {
                    left: 2px;
                }
            }
        }
    }
}